import Vue from 'vue'
import VueRouter from 'vue-router'

// 解决导航栏或者底部导航tabBar中的vue-router在3.0版本以上频繁点击菜单报错的问题。
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'main',
    component: () => import( '../views/Main.vue'),
    redirect: '/home',  //重定向到主页
    children:[
      {path: 'home' ,name: 'Home', meta:{name:'系统首页'}, component: () =>import( '../views/Main/Home.vue')},
      {path: 'person' ,name: 'Person', meta:{name:'账户设置'}, component: () =>import( '../views/Main/Person.vue')},
      {path: '/taskCreate', name: 'TaskCreate', meta:{name:'任务创建'}, component: ()=>import('../views/Main/TaskCreate.vue')},
      {path: '/taskView', name: 'TaskView', meta:{name:'任务查看'}, component: ()=>import('../views/Main/TaskView.vue')},
      {path: '/deviceList', name: 'DeviceList', meta:{name:'设备列表'}, component: ()=>import('../views/Main/DeviceList.vue')},
      {path: '/basicInfo', name: 'BasicInfo', meta:{name:'基本信息'}, component: ()=>import('../views/Main/BasicInfo.vue')},
      {path: '/maintain', name: 'Maintain', meta:{name:'维护记录'}, component: ()=>import('../views/Main/Maintain.vue')},
      {path: '/checkRecord', name: 'CheckRecord', meta:{name:'点检记录'}, component: ()=>import('../views/Main/CheckRecord.vue')},
      {path: '/entryForm', name: 'EntryForm', meta:{name:'选择录入形式'}, component: ()=>import('../views/Main/EntryForm.vue')},
      {path: '/chooseDevice', name: 'ChooseDevice', meta:{name:'勾选设备及状态'}, component: ()=>import('../views/Main/ChooseDevice.vue')},
      {path: '/performance', name: 'Performance', meta:{name:'签到情况'}, component: ()=>import('../views/Main/Performance.vue')},
      {path: '/standard', name: 'Standard', meta:{name:'操作标准'}, component: ()=>import('../views/Main/Standard.vue')},
      {path: '/standardView', name: 'StandardView', meta:{name:'运行工标准查看'}, component: ()=>import('../views/Main/StandardView.vue')},
      {path: '/standardView2', name: 'StandardView2', meta:{name:'维修工标准查看'}, component: ()=>import('../views/Main/StandardView2.vue')},
      {path: '403' ,name: 'Auth', meta:{name:'无权限'}, component: () =>import( '../views/Main/Auth.vue')},
      {path: '/user', name: 'User', meta:{name:'用户信息'}, component: ()=>import('../views/Main/User.vue')},
      {path: '/notice', name: 'Notice', meta:{name:'系统公告'}, component: ()=>import('../views/Main/Notice.vue')},
      {path: '/problem', name: 'Problem', meta:{name:'问题反馈'}, component: ()=>import('../views/Main/Problem.vue')},
      {path: '/problemDetail', name: 'ProblemDetail', meta:{name:'问题详情'}, component: ()=>import('../views/Main/ProblemDetail.vue')},
      {path: '/childDevice', name: 'ChooseDevice', meta:{name:'子设备表'}, component: ()=>import('../views/Main/ChildDevice.vue')},
      {path: '/realData', name: 'RealData', meta:{name:'实时数据表'}, component: ()=>import('../views/Main/RealData.vue')},
      {path: '/numericValue', name: 'NumericValue', meta:{name:'数值表标准'}, component: ()=>import('../views/Main/NumericValue.vue')},
      {path: '/maintainInfo', name: 'MaintainInfo', meta:{name:'设备维护记录详情'}, component: ()=>import('../views/Main/MaintainInfo.vue')},
      {path: '/checkRecordInfo', name: 'CheckRecordInfo', meta:{name:'设备点检记录详情'}, component: ()=>import('../views/Main/CheckRecordInfo.vue')},
      {path: '/meterList', name: 'meterList', meta:{name:'仪表列表'}, component: ()=>import('../views/Main/MeterList.vue')},
      {path: '/meterList1', name: 'meterList1', meta:{name:'运行工标准查看'}, component: ()=>import('../views/Main/MeterList1.vue')},
      {path: '/meterList2', name: 'meterList2', meta:{name:'维修工标准查看'}, component: ()=>import('../views/Main/MeterList2.vue')},
      {path: '/password', name: 'Password', meta:{name:'修改密码'}, component: ()=>import('../views/Main/Password.vue')},
      {path: '/feedback', name: 'Feedback', meta:{name:'点检记录异常详情'}, component: ()=>import('../views/Main/Feedback.vue')},
      {path: '/recordTask', name: 'RecordTask', meta:{name:'任务记录'}, component: ()=>import('../views/Main/RecordTask.vue')},
      {path:'/taskTemp',name: 'taskTemp',meta:{name:'临时任务详情'},component: ()=>import('../views/Main/TaskTemp.vue')},
      {path: '/taskCheckMeter', name: 'TaskCheckMeter', meta:{name:'仪表点检任务详情'}, component: ()=>import('../views/Main/TaskCheckMeter.vue')},
      {path: '/taskCheckDevice', name: 'TaskCheckDevice', meta:{name:'设备点检任务详情'}, component: ()=>import('../views/Main/TaskCheckDevice.vue')},
    ]
  },
  {path: '/login', name: 'Login',meta:{name:'登录'},  component: ()=>import('../views/Login.vue')},
  {path: '*', name: '404',meta:{name:'无法访问'},  component: ()=>import('../views/404.vue')},

]

const router = new VueRouter({
  // mode: 'history',
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to,from,next)=>{
  //  to 是到达的路由信息
  //from 是开源的路由信息
  //next 是帮助我们跳转路由的函数
  let adminPath =['/123']
  let user = JSON.parse(localStorage.getItem('honey-user') || '{}')

  if (user.role !== '管理员' && adminPath.includes(to.path)){     //如果当前登陆的用户不是管理员，然后当前到达的路径是管理员才有权限访问的路径，那这个时候我就让用户去到一个没有权限的页面，不让他访问实际的页面
    next('/403')
  } else {
    next()
  }
})

export default router
